.secondary-title {
  font-size: 46px;
  font-weight: 800;
  line-height: 1.3rem;
  letter-spacing: 0.03rem;
}

.secondary-title--light-theam {
  color: #111518;
}

.secondary-title--dark-theam {
  color: #fff;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .secondary-title {
    font-size: 20px;
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .secondary-title {
    font-size: 30px;
  }
}

@media screen and (min-width: 1200px) {
